import React, { Component } from "react";
import arrowblue from "../static/imges/arrowblue.svg";
import styled from "styled-components";
import { Link } from "react-router-dom";

//#region
const Button = styled.button`
  width: 120px;
  height: 40px;
  border-radius: 2px;
  background-color: #ffffff;
  border: none;

  font-size: 15px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #0c128f;
`;
//#endregion

export default class Information extends Component {
  render() {
    return (
      <div className="container inform">
        <div
          className="row mx-0 px-0"
          style={{ paddingTop: "60px", paddingBottom: "60px" }}
        >
          <div className="col tit"></div>
          <div className="col-auto p-0">
            <table>
              <tbody>
                <tr>
                  <td>
                    <img
                      alt="back"
                      src={arrowblue}
                      className="arrow"
                      id="left"
                      onClick={this.arrowClick}
                    />
                  </td>
                  <td>
                    <hr className="line" />
                  </td>
                  <td>
                    <img
                      alt="next"
                      src={arrowblue}
                      className="arrow"
                      id="right"
                      onClick={this.arrowClick}
                    />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div
            className="row horizScroll"
            style={{
              whiteSpace: "nowrap",
              display: "inline-block",
              margin: "40px 0px",
              pdding: "0px"
            }}
          >
            <div className="card col" style={{ display: "inline-block" }}>
              <div className="description-info"> </div>
              <div>
                   (. Currency <br /> corridor) 
                 , <br />    
                <br />
                    <br />  .
              </div>
              <br />
              <br />
              <br />
              <br />
              <br />
              <br />
              <br />
              <Button className="mt-auto p-2">
                <Link
                  className="linkDecoration"
                  to="information/valutecorridor"
                >
                  
                </Link>
              </Button>
            </div>

            <div className="card col" style={{ display: "inline-block" }}>
              <div className="description-info">
                  <br />
              </div>
              <div>
                   (. Currency <br /> Arbitrage) 
                 <br />      <br />
                     <br />    .
              </div>
              <br />
              <br />
              <br />
              <br />
              <br />
              <br />
              <br />
              <Button className="mt-auto p-2">
                <Link className="linkDecoration" to="information/valutearbitr">
                  
                </Link>
              </Button>
            </div>

            <div
              className="card col"
              style={{ display: "inline-block", top: "10px" }}
            >
              <div className="description-info">
                  <br />   <br />
                
              </div>
              <div>
                    <br />   
                  <br /> /  <br />
                 ,   <br />  .
              </div>
              <br />
              <br />
              <br />
              <br />
              <Button className="mt-auto p-2">
                <Link
                  className="linkDecoration"
                  to="information/valutearbitrcor"
                >
                  
                </Link>
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



