import React from "react";
import styled from "styled-components";
//#region
const Text = styled.div`
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.47;
  letter-spacing: normal;
  font-family: Gravity;
`;
const Title = Text.extend`
  font-size: 36px;
  font-weight: bold;
  color: #0c128f;
  font-family: Gravity;
`;
const Digit = Title.extend`
  font-size: 60px;
`;
//#endregion
export const RulesFereralProgram = () => {
  return (
    <div className="container">
      <Title
        className="row mx-0"
        style={{ paddingTop: "60px", paddingBottom: "20px" }}
      >
           :
      </Title>
      <div className="row">
        <div className="col-4 ">
          <div className="digitBox mx-0">
            <Digit>01</Digit>
          </div>
          <Text>
                (  
             ,     -, 
            e-mail-  .)   .
          </Text>
        </div>
        <div className="col-4 p-0">
          <div className="digitBox mx-0">
            <Digit>02</Digit>
          </div>
          <Text>
            :    Corridor   ,
               Corridor     
            Corridor  .
          </Text>
        </div>
        <div className="col-4 p-0">
          <div className="digitBox mx-0">
            <Digit>03</Digit>
          </div>
          <Text>
                 Corridor 
            -    ,  
               .
          </Text>
        </div>
      </div>
      <div className="row">
        <div className="col-8">
          <div className="digitBox mx-0">
            <Digit>04</Digit>
          </div>
          <Text>
                 ,  
                     
             Corridor.        
            Corridor  .
          </Text>
        </div>
        <div className="col-3 p-0">
          <div className="digitBox mx-0">
            <Digit>05</Digit>
          </div>
          <Text>
                 
            .
          </Text>
        </div>
      </div>
    </div>
  );
};



